.ecl-search-form{display:flex;margin:0;position:relative;width:100%}.ecl-search-form .ecl-form-group{display:flex;flex-grow:1;max-width:100%}.ecl-search-form__label{clip:rect(0,0,0,0);border:0;height:1px;margin:-1px;overflow:hidden;padding:0;position:absolute;visibility:visible;white-space:nowrap;width:1px}.ecl-search-form__text-input{border-end-end-radius:0;border-inline-end-width:0;border-start-end-radius:0;flex-grow:1;margin-top:0!important;width:100%}.ecl-search-form__button{-webkit-border-end:1px solid #707070;background-color:#ebebeb;border-bottom:1px solid #707070;border-end-start-radius:0;border-inline-end:1px solid #707070;border-inline-start-width:0;border-start-start-radius:0;border-top:1px solid #707070;color:#004494;padding:calc(.75rem - 1px)}.ecl-search-form__button:active,.ecl-search-form__button:hover{background-color:#cfcfcf}.ecl-search-form__button:focus-visible{outline:2px solid #004494;outline-offset:-2px}.ecl-search-form__button[disabled]{background-color:hsla(0,0%,92%,.5);border-color:hsla(0,0%,44%,.5);color:rgba(0,68,148,.5)}.ecl-search-form__button[disabled]:hover{background-color:hsla(0,0%,81%,.5);color:rgba(0,68,148,.5)}.ecl-search-form__button .ecl-button__label{display:none}.ecl-search-form__button .ecl-button__icon{fill:currentcolor;display:block;margin:0}@media (min-width:768px){.ecl-search-form{max-width:25rem}.ecl-search-form__button{-webkit-padding-start:calc(1rem - 1px);-webkit-padding-end:calc(1rem - 1px);padding-inline-end:calc(1rem - 1px);padding-inline-start:calc(1rem - 1px)}.ecl-search-form__button .ecl-button__label{display:block}.ecl-search-form__button .ecl-button__icon{display:none}}@media (min-width:996px){.ecl-search-form{max-width:31.25rem}}.ecl-search-form__text-input .ecl-text-input{width:100%!important}.ecl-search-form__button.sc-ecl-search-form-ec{background:#ebebeb!important;border-bottom:1px solid #707070!important;border-right:1px solid #707070!important;border-top:1px solid #707070!important;padding:calc(.75rem - 1px)!important}@media (width >= 768px){.ecl-search-form__button.sc-ecl-search-form-ec{-webkit-padding-start:calc(1rem - 1px)!important;-webkit-padding-end:calc(1rem - 1px)!important;padding-inline-end:calc(1rem - 1px)!important;padding-inline-start:calc(1rem - 1px)!important}}.ecl-search-form__text-input.sc-ecl-search-form-ec:not(:focus-visible){border-inline-end-color:transparent!important}